import {api} from '/js/api';
import {Languages} from '/js/util';


api({});


new Swiper('#slider', {
	slidesPerView: 'auto',
	centeredSlides: true,
	loop: true,
	loopFillGroupWithBlank: true,
	spaceBetween: 30,
});

let tplDetail = template.compile(document.getElementById('tpl-detail').innerHTML);
$('#slider').on('click', '.cont', function () {
	let data = {
		title: 'tuniu travel'
	};
	showMask(tplDetail(data));
});


$('#like').click(function () {
	showResult(20);
});


let tplResult = template.compile(document.getElementById('tpl-result').innerHTML);

function showResult(percent: number) {
	let data = {
		result: percent,
		resultTitle: Languages.package.resultTitle,
		selected: Languages.package.selected,
		atLeast: Languages.package.atLeast,
		atMost: Languages.package.atMost,
	};

	showMask(tplResult(data));

}

function showMask(htmlContent: string) {
	let mask = $(`<div class="mask" style="background-color: rgba(0,0,0,0.6)"></div>`);
	mask.on('click', function () {
		mask.remove();
	});

	mask.append(htmlContent);
	$('body').append(mask);
}